import { Component, OnInit } from "@angular/core";

import { DogService } from "../services/dog.service";
import { Dog } from "../models/dog";
import { Message } from "primeng/components/common/api";
import { MessageService } from "primeng/components/common/messageservice";
import { TranslateService } from "@ngx-translate/core";

@Component({
  selector: "app-dog",
  templateUrl: "./dog.component.html",
  styleUrls: ["./dog.component.css"]
})
export class DogComponent implements OnInit {
  constructor(
    private dogservice: DogService,
    private messageService: MessageService,
    private _translate: TranslateService
  ) {}

  dogs: Dog[] = [];
  newDog: boolean = false;
  dog: Dog;
  selectedDog: Dog;
  displayDialog: boolean;
  cols = [
    { field: "name", header: "dog.name" },
    { field: "years", header: "dog.year" },
    { field: "variety", header: "dog.kind" }
  ];

  ngOnInit() {
    this.getdog();
  }

  /**
   * 取得所有的資料
   */
  getdog() {
    this.dogservice.getdogs().subscribe(
      (data: { data: Dog[] }) => {
        this.dogs = data.data;
      },
      err => {
        console.error(err);
      }
    );
  }

  /**
   * 點選一筆資料，並開窗編輯畫面
   * @param event 取得點選的那筆資料
   */
  onRowSelectDog(event) {
    this.newDog = false;
    this.dog = { ...event.data };
    this.displayDialog = true;
  }

  /**
   * 新增一筆資料
   */
  onCreateDog() {
    this.newDog = true;
    this.dog = new Dog();
    this.displayDialog = true;
  }

  /**
   * 新增或修改一筆資料
   */
  onSaveDog() {
    if (!this.onDisabledRequired()) {
      const dogs = [...this.dogs];
      if (this.newDog) {
        dogs.push(this.dog);
        this.messageService.add({
          severity: "success",
          summary: 'Service Message',
        detail: this.getTranslate('message.successfully_added')
        });
      } else {
        const index = dogs.indexOf(this.selectedDog);
        console.log(index);
        dogs[index] = this.dog;
        this.messageService.add({
          severity: "success",
          summary: 'Service Message',
        detail: this.getTranslate('message.successfully_modified')
        });
      }

      this.dogs = dogs;
      this.newDog = false;
      this.onCancelDog();
    } else {
      this.messageService.add({
        severity: "error",
        summary: 'Service Message',
        detail: this.getTranslate('message.fields_that_are_required')
      });
    }
  }

  /**
   * 刪除一筆資料
   */
  onDeleteDog() {
    const index = this.dogs.indexOf(this.selectedDog);
    this.dogs = this.dogs.filter((val, i) => i !== index);
    this.messageService.add({
      severity: "success",
      summary: "Service Message",
      detail: this.getTranslate("message.successfully_deleted")
    });
    this.onCancelDog();
  }

  // onChang(){
  //   this.dog.change = true;
  //   return this.dog.change;
  // }

  /**
   * 取消
   */
  onCancelDog() {
    // this.dog.change = false;
    this.dog = this.selectedDog = null;
    this.displayDialog = false;
  }

  /**
   * 檢查必填欄位，是否都有填
   */
  onDisabledRequired() {
    if (this.dog) {
      if (!(this.dog.name && this.dog.years && this.dog.variety)) {
        return true;
      }
      return false;
    }
  }

  /**
   * 多國語言
   */
  getTranslate(key: string): string {
    this._translate.get(key).subscribe(res => (key = res));
    return key;
  }
}
